import React, { Component } from "react";
import { BrowserRouter as Router, Switch, Route } from "react-router-dom";

import Web3 from "web3";
import LotteryJson from "./abis/LotteryContract.json";

import "bootstrap/dist/css/bootstrap.min.css";
import "bootstrap-icons/font/bootstrap-icons.css";

import Main from "./components/Main";

import "./css/main.css";
import Navbar from "./components/Navbar";
import WinnerHistory from "./components/WinnerHistory";
import Banner from "./components/Banner";
import Aboutus from "./components/Aboutus";
import Howtoplay from "./components/Howtoplay";
import SmartContract from "./components/SmartContract";

class App extends Component {

  async componentWillMount() {
    this.setState({ loading: false });

  }

  async ConnectAccount() {
    await this.loadWeb3();
    await this.loadBlockChatData();
  };

  async loadWeb3() {
    if (window.ethereum) {
      window.web3 = new Web3(window.ethereum);
      await window.ethereum.enable();

    } else if (window.web3) {
      window.web3 = new Web3(window.web3.currentProvider);
    } else {
      window.alert(
        "Non-Ethereum browser detected. You should consider trying MetaMask!"
      );
    }
  }

  // createAccount = async () => {
  //     const UserName = document.getElementById("username").value;
  //     await this.state.blockChatContract.methods.createAccount(UserName)
  //         .send({ from: this.state.account })
  //         .on('transactionHash', function (hash) {

  //         })
  //         .on('receipt', function (receipt) {

  //         })
  //         .on('confirmation', (confirmationNumber, receipt) => {
  //             this.setState({ accountCreated: true });
  //         })
  //         .on('error', function (error, receipt) {

  //         });

  // }

  async loadBlockChatData() {
    const web3 = window.web3;
    const accounts = await web3.eth.getAccounts();
    web3.eth.defaultAccount = accounts[0];
    this.setState({ account: accounts[0] });

    const networkId = await web3.eth.net.getId();
    // const networkData = LotteryJson.networks[networkId];

    if (networkId === 80001 || networkId === 137) {
      this.setState({ connected: true });
      const networkAddress = '0x67F134A2B65553f4106A51fC2A0b66CF4DDF3a50';
      const lotteryContract = await web3.eth.Contract(LotteryJson.abi, networkAddress);
      this.setState({ lotteryContract });

      const gameStartDate = await lotteryContract.methods.getGameStartDate().call();
      this.setState({ gameStartDate: gameStartDate.toNumber() });

      this.setState({ loading: false });
    } else {
      window.alert("Blockchat contract not deployed to detected network.");
    }
  }

  constructor(props) {
    super(props);
    this.state = {
      connected: false,
      account: "",
      gameStartDate: 0,
      lotteryContract: null,
      loading: true,
    };

    this.ConnectAccount = this.ConnectAccount.bind(this)
  }

  render() {
    return (!this.state.loading
      ?
      <Router>
        <Navbar account={this.state.account} ConnectAccount={this.ConnectAccount} />
        <Switch>
          <Route path="/" exact>
            <Banner gameStartDate={this.state.gameStartDate} />
            <Main account={this.state.account} connected={this.state.connected} ConnectAccount={this.ConnectAccount} lotteryContract={this.state.lotteryContract} />
          </Route >
          <Route path="/referral/:referCode" exact>
            <Banner gameStartDate={this.state.gameStartDate} />
            <Main isRefferalAvailable={true} account={this.state.account} connected={this.state.connected} ConnectAccount={this.ConnectAccount} lotteryContract={this.state.lotteryContract} />
          </Route >
          <Route path="/winner-history" exact>
            <WinnerHistory lotteryContract={this.state.lotteryContract} connected={this.state.connected} />
          </Route >
          <Route path="/about" exact>
            <Aboutus />
          </Route >
          <Route path="/how-to-play" exact>
            <Howtoplay />
          </Route >
          <Route path="/smart-contract" exact>
            <SmartContract />
          </Route >
        </Switch >
      </Router >
      :
      <div className="text-center">
        loading
      </div>

    );
  }
}

export default App;
