import React, { useEffect, useState } from 'react'
import "../css/CountDown.css"

export default function CountDown(props) {
    const [timerDays, setTimerDays] = useState();
    const [timerHours, setTimerHours] = useState();
    const [timerMinutes, setTimerMinutes] = useState();
    const [timerSeconds, setTimerSeconds] = useState();

    let interval;


    function timeConverter(UNIX_timestamp){
        var a = new Date(UNIX_timestamp * 1000);
        var months = ['Jan','Feb','Mar','Apr','May','Jun','Jul','Aug','Sep','Oct','Nov','Dec'];
        var year = a.getFullYear();
        var month = months[a.getMonth()];
        var date = a.getDate();
        var hour = a.getHours();
        var min = a.getMinutes();
        var sec = a.getSeconds();
        var time = date + ' ' + month + ' ' + year + ' ' + hour + ':' + min + ':' + sec ;
        return time;
      }

    const startTimer = () => {
        // const startDate = timeConverter(props.gameStartDate);
        // const countDownDate = new Date(startDate).getTime();

        // console.log(startDate);
        const countDownDate = props.gameStartDate.toString()+"000";
        
        
        interval = setInterval(() => {
            const now = new Date().getTime();

            const distance = countDownDate - now;

            const days = Math.floor(distance / (24 * 60 * 60 * 1000));
            const hours = Math.floor(
                (distance % (24 * 60 * 60 * 1000)) / (1000 * 60 * 60)
            );
            const minutes = Math.floor((distance % (60 * 60 * 1000)) / (1000 * 60));
            const seconds = Math.floor((distance % (60 * 1000)) / 1000);

            if (distance < 0) {
                // Stop Timer

                clearInterval(interval.current);
            } else {
                // Update Timer
                setTimerDays(days);
                setTimerHours(hours);
                setTimerMinutes(minutes);
                setTimerSeconds(seconds);
            }
        });
    };

    useEffect(() => {
        startTimer();
    });

    const countDownStyle = "py-2 rounded text-center";

    const countDownDotStyle = "mx-2 my-auto";

    return (
        <div className=" text-light mx-auto text-center">

            <div className="glow-div2 glassBg-box-timer d-flex  justify-content-center  align-items-center">
                <span className={countDownStyle}>
                    <h2>{timerDays}</h2>
                    <p>Days</p>
                </span>
                <span className={countDownDotStyle}>
                    <h1>:</h1>
                </span>
                <span className={countDownStyle}>
                    <h2>{timerHours}</h2>
                    <p>Hours</p>
                </span>
                <span className={countDownDotStyle}>
                    <h1>:</h1>
                </span>
                <span className={countDownStyle}>
                    <h2>{timerMinutes}</h2>
                    <p>Minutes</p>
                </span>
                <span className={countDownDotStyle}>
                    <h1>:</h1>
                </span>
                <span className={countDownStyle}>
                    <h2>{timerSeconds}</h2>
                    <p>Secounds</p>
                </span>
            </div>
        </div>
    )
}
