import React, { useEffect, useState } from 'react'
import CountDown from './CountDown'

export default function Banner(props) {
    const [sholdClockShow, setSholdClockShow] = useState(true);
    useEffect(() => {
        const now = new Date().getTime();
        if (now >= props.gameStartDate) {
            setSholdClockShow(false);
        } else {
            setSholdClockShow(true);

        }
    })
    return (
        <div className="banner-img-bg mx-md-5 text-light  py-4 ">
            {/* <img className=" img-fluid banner-img" src="img/banner/1.jpg" alt="" /> */}
            <div className="d-md-flex align-items-center justify-content-between text-center">

                {sholdClockShow
                    ?
                    <h2 className="mr-md-5 my-4">First Round!</h2>
                    :
                    <div className="glow-div2 glassBg-box-timer text-center">
                        <h2 className="my-4">Play Game And Earn</h2>
                    </div>

                }

                {sholdClockShow &&
                    <CountDown gameStartDate={props.gameStartDate} />
                }
                {sholdClockShow &&
                    <div className="text-center ml-md-5">
                        <h5 className="">22TH November,</h5>
                        <h5 className="">5PM UTC</h5>
                    </div>
                }
            </div>

        </div>
    )
}
